.container {
  display: flex;

  .card {
    padding: 10px;
    background-color: $color-grey;
    &__image {

    }
    &__actions {
      a {
      }
    }
    &:first-child {
      .card__actions {
        a {
          @include button($color-green);
          color: blue;
        }
      }  
    }
    &:nth-child(2) {
      .card__actions {
        a {
          @include button($color-purple);
        }
      }
    }
    &:last-child {
      .card__actions {
        a {
          @include button($color-red);
        }
      }
    }
  }
}
