.container {
  display: flex;
}
.container .card {
  padding: 10px;
  background-color: #808080;
}
.container .card:first-child .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #41e657;
  color: #fff;
  color: blue;
}
.container .card:nth-child(2) .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #800080;
  color: #fff;
}
.container .card:last-child .card__actions a {
  height: 39px;
  width: 110px;
  font-size: 22px;
  padding: 0.5em;
  background-color: #ff0000;
  color: #fff;
}